.Slider {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px; }

.Slider input {
  display: none; }

.slide-span {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: white;
  -webkit-transition: .4s;
  -o-transition: .4s;
  transition: .4s; }

.slide-span:before {
  position: absolute;
  content: "";
  height: 22px;
  width: 22px;
  top: 3px;
  left: 3px;
  background-color: black;
  -webkit-transition: .4s;
  -o-transition: .4s;
  transition: .4s; }

input:checked + .slide-span {
  background-color: #f9fc53; }

input:checked + .slide-span:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px); }

/* Rounded slide-spans */
.slide-span {
  border: 3px solid black;
  border-radius: 34px; }

.slide-span:before {
  border-radius: 50%; }



