.Increment {
  display: grid;
  grid-template-columns: 50px auto;
  grid-template-rows: 30px 30px; }

.inc-num {
  font-size: 30px;
  font-weight: bold;
  grid-row: 1/3;
  grid-column: 1/2;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-pack: center;
      justify-content: center;
  vertical-align: middle; }
  .inc-num span {
    margin: auto; }

.up {
  grid-row: 1/2;
  grid-column: 2/3; }

.down {
  grid-row: 2/3;
  grid-column: 2/3;
  -webkit-transform: rotate(180deg);
      -ms-transform: rotate(180deg);
          transform: rotate(180deg); }

.Increment button {
  cursor: pointer;
  font-size: 20px;
  font-weight: bold;
  border-radius: 30px;
  width: 30px;
  height: 30px;
  border: 2px solid black;
  background-color: white; }

.Increment button:hover {
  background-color: yellow; }



