.Clicker {
  cursor: pointer;
  font-size: 18px;
  width: 9em;
  padding: 8px;
  border-radius: 20px;
  border: 3px solid black;
  font-weight: bold;
  text-transform: capitalize;
  background-color: #FFF;
  margin: 0 5px; }

.Clicker:hover {
  -webkit-transition: 0.5s ease;
  -o-transition: 0.5s ease;
  transition: 0.5s ease;
  background-color: #f9fc53;
  /*background-color: #DDD;*/ }

.ClickSelect {
  background-color: #f9fc53; }



